@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: 'Inter';
  background-color: #f2f2f2;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.add-to-cart-btn {
  width: 100%;
}

.add-to-cart-btn button {
  width: 100%;
  border: none;
  display: flex;
  cursor: pointer;
  padding: 12px 0;
  font-size: 16px;
  color: #ffffff;
  font-weight: bold;
  position: relative;
  border-radius: 5px;
  align-items: center;
  background: #40B25C;
  text-decoration: none;
  justify-content: center;
  text-transform: uppercase;
}

.add-to-cart-btn button:hover {
  background: #1C6C3E;
  transition: background-color 0.5s ease;
}

.add-to-cart-btn button.added {
  color: #1C1C1C;
  background: #A3F9B9;
}

.add-to-cart-btn button.added:before {
  content: '';
  width: 16px;
  height: 16px;
  margin-right: 10px;
  background-size: contain;
  background: url(data:image/png;base64,) no-repeat center;
}
.wishlist-button {
  top: 0;
  right: 0;
  cursor: pointer;
  position: absolute;
}

.wishlist-button:hover circle, .wishlist-button:hover path {
  fill: #FEE5EC;
  transition: fill 0.3s ease;
}

.wishlist-button:hover path {
  stroke: #DA4B4F;
  transition: stroke 0.3s ease;
}

.wishlist-button.wished circle {
  fill: #DA4B4F;
  transition: fill 0.3s ease;
}

.wishlist-button.wished:hover circle {
  fill: #C22539;
  transition: fill 0.3s ease;
}

.wishlist-button.wished path {
  fill: #ffffff;
  stroke: #ffffff;
  transition: stroke 0.3s ease;
}
.shelf-item {
  width: 304px;
  padding: 32px;
  display: flex;
  border-radius: 8px;
  align-items: center;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
}

.shelf-item:hover {
  transition: box-shadow 0.5s ease;;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.08);
}

.prodcut-image {
  width: 240px;
  height: 240px;
  margin: 0 0 32px;
  position: relative;
}

.product-name {
  margin-bottom: 24px;
}

.product-name .name {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
  text-align: left;
  color: #1C1C1C;
  line-height: 20px;
}

.product-price {
  width: 100%;
  display: flex;
  margin-bottom: 32px;
  flex-direction: column;
  align-items: flex-start;
}

.product-price .price {
  margin: 0;
  font-size: 14px;
  color: #b5b5b6;
  font-weight: 500;
  line-height: 20px;
  text-decoration: line-through;
}

.product-price .list-price {
  margin: 4px 0;
  font-size: 20px;
  font-weight: 600;
  color: #da4b4f;
  line-height: 20px;
}

.product-price .product-installments {
  margin: 4px 0;
  font-size: 12px;
  font-weight: 500;
  color: #1C1C1C;
  text-align: left;
}

.product-price .product-installments span {
  color: #848587;
}

@media screen and (max-width: 500px) {
  .shelf-item {
    width: 47%;
    padding: 15px;
  }

  .prodcut-image {
    width: 100%;
    height: auto;
  }

  .prodcut-image img {
    max-width: 100%;
  }

  .product-name .name {
    font-size: 13px;
  }
}
.product-showcase {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
